/* ============================================
   üöÄ OPTIMIZED CONTACT SECTION
   ============================================ */

.contact-main {
  width: 100%;
  min-height: 100vh;
  padding-top: 100px;
  margin: 0;
  
  /* üöÄ OPTIMIZATION: Lazy Render the entire section */
  content-visibility: auto;
  contain-intrinsic-size: 1000px; /* Approximate height to prevent scrollbar jump */
}

.basic-contact {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  padding: 20px 10px;
}

/* Flex Layouts */
.contact-heading-div,
.blog-heading-div,
.address-heading-div {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.contact-heading-div { align-items: flex-start; }
.address-heading-div { margin-top: 80px; }

/* Flex Children Equal Width */
.blog-heading-div > *,
.contact-heading-div > *,
.address-heading-div > * {
  flex: 1;
}

.contact-heading-img-div {
  margin-left: 13%;
  margin-bottom: 8%;
  margin-top: 4%;
  /* üöÄ OPTIMIZATION: Prevent layout shift for image container */
  min-height: 300px; 
}

/* ============================================
   üó∫Ô∏è OPTIMIZED MAP CONTAINER
   ============================================ */
.map-div {
  width: 80%;
  max-width: 900px;
  /* üöÄ OPTIMIZATION: Use aspect-ratio instead of fixed height for better resizing */
  aspect-ratio: 16 / 9;
  height: auto; 
  min-height: 400px; /* Fallback */
  margin: 40px auto;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  background: rgba(74, 0, 224, 0.05); /* Simplified background */
  padding: 3px;
  
  /* üöÄ OPTIMIZATION: Hardware Acceleration */
  transform: translateZ(0);
  will-change: transform, box-shadow;
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275), box-shadow 0.4s ease;
}

.map-div::before,
.map-div::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 16px;
  pointer-events: none;
  transition: opacity 0.4s ease;
}

.map-div::before {
  padding: 3px;
  background: linear-gradient(135deg, #4a00e0, #8e2de2);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0.7;
}

.map-div::after {
  padding: 1px;
  background: linear-gradient(135deg, rgba(74, 0, 224, 0.3), rgba(142, 45, 226, 0.3));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  /* üöÄ OPTIMIZATION: Prepare opacity change */
  will-change: opacity; 
}

.map-div:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(74, 0, 224, 0.2), 0 0 0 1px rgba(74, 0, 224, 0.1);
}

.map-div:hover::before { opacity: 1; }
.map-div:hover::after {
  opacity: 1;
  animation: borderGlow 1.5s linear infinite;
}

.map-div iframe {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 16px;
  /* üöÄ OPTIMIZATION: Filter is expensive. Use sparingly. */
  filter: contrast(1.05) saturate(1.05);
  transition: filter 0.4s ease;
  will-change: filter;
}

.map-div:hover iframe {
  filter: contrast(1.1) saturate(1.1);
}

.contact-section-map {
  font-size: 24px;
  font-weight: 600;
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
  padding: 0 15px;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
  
  /* Text Shine Effect */
  background: linear-gradient(90deg, #fff 0%, #fff 45%, #e0e0e0 50%, #fff 55%, #fff 100%);
  background-size: 200% auto;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  
  /* üöÄ OPTIMIZATION: Hint the browser about background animation */
  will-change: background-position;
  animation: shine 3s linear infinite;
}

.contact-section-map::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #fff, transparent);
  transform: scaleX(0.8);
  opacity: 0.7;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.contact-section-map:hover::after {
  transform: scaleX(1);
  opacity: 1;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

@keyframes shine {
  0% { background-position: -200% center; }
  100% { background-position: 200% center; }
}

@keyframes borderGlow {
  0%, 100% { opacity: 0.5; }
  50% { opacity: 1; }
}

/* ============================================
   üñºÔ∏è IMAGES & TEXT ALIGNMENT
   ============================================ */
.profile-pic {
  box-shadow: 0 2px 7px #353535;
  border-radius: 2px;
  /* üöÄ OPTIMIZATION: Prevent CLS on images */
  height: auto;
  max-width: 100%;
  display: block;
}

.blog-heading-img-div,
.contact-heading-text-div,
.blog-heading-text-div,
.address-heading-text-div {
  text-align: center;
}

.contact-heading-img-div > *,
.blog-heading-img-div > * {
  max-width: 100%;
  height: auto;
}

/* ============================================
   ‚úçÔ∏è TYPOGRAPHY ANIMATIONS
   ============================================ */
/* Consolidated Text Styles to reduce code duplication */
.contact-heading-text,
.blog-heading-text,
.address-heading-text,
.contact-header-detail-text,
.blog-header-detail-text {
  font-family: "Google Sans Medium", sans-serif;
  text-align: center;
  background: linear-gradient(90deg, #fff 0%, #fff 45%, #e0e0e0 50%, #fff 55%, #fff 100%);
  background-size: 200% auto;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  
  /* üöÄ OPTIMIZATION: Efficient Animation */
  will-change: background-position;
  animation: shine 4s linear infinite;
}

.contact-heading-text,
.blog-heading-text {
  font-size: 56px;
  font-weight: 400;
  line-height: 1.1;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
}

.contact-heading-text { margin-top: 60px; }
.blog-heading-text { margin-top: 40px; animation-delay: 0.5s; }

.address-heading-text {
  font-size: 30px;
  font-family: "Google Sans Regular";
  margin-bottom: 10px;
  animation-delay: 0.2s;
}

.contact-header-detail-text,
.blog-header-detail-text {
  font-size: 20px;
  line-height: 30px;
  margin: 0 20px;
}

.contact-header-detail-text { animation-delay: 0.3s; }
.blog-header-detail-text { animation-delay: 0.7s; }

/* Hover Text Effect */
.contact-heading-text:hover,
.blog-heading-text:hover,
.address-heading-text:hover {
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.4);
  transition: text-shadow 0.3s ease;
}

/* ============================================
   üîò BUTTONS (Optimized)
   ============================================ */
.general-btn-div,
.blogsite-btn-div,
.address-btn-div {
  width: 100%;
  display: flex;
  justify-content: center;
}

.button-container {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  flex-wrap: nowrap;
  max-width: 500px;
}

/* Base style for all action buttons */
.general-btn,
.button-contact,
.github-btn {
  text-decoration: none;
  color: #fff;
  padding: 12px 24px;
  margin-top: 25px;
  border-radius: 12px;
  border: 2px solid transparent;
  min-width: 180px; /* Consistent width */
  height: 50px;
  font-weight: bold;
  font-family: "Google Sans Regular", sans-serif;
  font-size: 17px;
  
  /* üöÄ OPTIMIZATION: Transform-only transitions are smoother */
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275), box-shadow 0.4s ease;
  
  cursor: pointer;
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  gap: 8px;
  
  /* üöÄ OPTIMIZATION: Hardware Acceleration */
  transform: translateZ(0); 
  will-change: transform, box-shadow;
}

/* Specific Colors */
.general-btn {
  background: linear-gradient(135deg, #4a00e0, #8e2de2);
  box-shadow: 0 6px 20px rgba(142, 45, 226, 0.3);
  animation: pulse 3s infinite;
}

.button-contact {
  background: linear-gradient(135deg, #ff416c, #ff4b2b);
  box-shadow: 0 6px 20px rgba(255, 65, 108, 0.3);
  animation: pulse-red 3s infinite;
}

.github-btn {
  background: linear-gradient(135deg, #333, #000);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Hover States */
.general-btn:hover {
  transform: translateY(-5px) scale(1.03);
  box-shadow: 0 12px 25px rgba(142, 45, 226, 0.4);
  background: linear-gradient(135deg, #5a10f0, #9e3df2);
}

.button-contact:hover {
  transform: translateY(-5px) scale(1.03);
  box-shadow: 0 12px 25px rgba(255, 65, 108, 0.4);
  background: linear-gradient(135deg, #ff517c, #ff5b3b);
}

.github-btn:hover {
  transform: translateY(-5px) scale(1.03);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  background: linear-gradient(135deg, #000, #333);
}

/* Button Shine Effect (Pseudo elements) */
.general-btn::before,
.button-contact::before {
  content: "";
  position: absolute;
  top: 0; left: 0; width: 100%; height: 100%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transform: translateX(-100%);
  transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  z-index: -1;
  will-change: transform;
}

.general-btn:hover::before,
.button-contact:hover::before {
  transform: translateX(100%);
}

.github-icon {
  font-size: 22px;
  transition: transform 0.3s ease;
  will-change: transform;
}

.github-btn:hover .github-icon {
  transform: rotate(360deg) scale(1.2);
}

@keyframes pulse {
  0%, 100% { box-shadow: 0 6px 20px rgba(142, 45, 226, 0.3); }
  50% { box-shadow: 0 6px 25px rgba(142, 45, 226, 0.5); }
}

@keyframes pulse-red {
  0%, 100% { box-shadow: 0 6px 20px rgba(255, 65, 108, 0.3); }
  50% { box-shadow: 0 6px 25px rgba(255, 65, 108, 0.5); }
}

/* ============================================
   üì± RESPONSIVE & UTILS
   ============================================ */
.fade-bottom {
  opacity: 0;
  transform: translateY(40px);
  animation: fade-bottom 1s ease-out forwards;
}

@keyframes fade-bottom {
  to { opacity: 1; transform: translateY(0); }
}

.testimonials-section { margin-top: 80px; margin-bottom: 60px; }

@media (max-width: 1380px) {
  .contact-heading-text, .blog-heading-text { font-size: 50px; }
}

@media (max-width: 768px) {
  .contact-main { padding: 0; }
  .basic-contact { width: 95%; padding: 0 10px; }
  
  /* Reset flex directions */
  .contact-heading-div, 
  .blog-heading-div, 
  .address-heading-div,
  .button-container { 
    flex-direction: column; 
    width: 100%; 
    gap: 20px;
  }
  
  .blog-heading-div { flex-direction: column-reverse; }

  /* Map Adjustment */
  .map-div {
    width: 100%;
    max-width: 100%;
    aspect-ratio: 4 / 3; /* More vertical space on mobile */
    min-height: 300px;
    margin: 20px 0;
  }

  /* Typography Adjustments */
  .contact-heading-text, .blog-heading-text { font-size: 30px; margin-top: 0; }
  .address-heading-text { font-size: 20px; }
  .contact-header-detail-text, .blog-header-detail-text { font-size: 16px; padding: 0 10px; margin: 0; }
  
  /* Button Adjustments */
  .general-btn, .button-contact, .github-btn {
    width: 100%;
    max-width: 100%;
    font-size: 16px;
  }
  
  /* Layout alignment */
  .contact-heading-img-div,
  .contact-heading-img-div-address {
    margin: 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

/* üöÄ ACCESSIBILITY: Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .contact-heading-text, .general-btn, .map-div, .map-div:hover, .fade-bottom {
    animation: none !important;
    transition: none !important;
    transform: none !important;
  }
}